"use client";

import React, { useState } from "react";
import { captureCookies } from "../lib/browserAutomation";
import { CsgoRollApi, Cookie } from "../lib/csgoApi";

export default function AutoWithdrawBot() {
  const [markup, setMarkup] = useState<number>(0);
  const [cookies, setCookies] = useState<Cookie[] | null>(null);
  const [status, setStatus] = useState<string>("");

  const handleLogin = async () => {
    setStatus("Opening browser for manual login...");
    try {
      const capturedCookies = await captureCookies("https://csgo-roll.com/login");
      // Convert puppeteer cookies to Cookie interface
      const formattedCookies = capturedCookies.map((cookieStr) => {
        const cookie = JSON.parse(cookieStr);
        return {
          name: cookie.name,
          value: cookie.value,
          domain: cookie.domain,
          path: cookie.path,
          expires: cookie.expires,
          httpOnly: cookie.httpOnly,
          secure: cookie.secure,
          sameSite: cookie.sameSite,
        };
      });
      setCookies(formattedCookies);
      setStatus("Login successful, cookies captured.");
    } catch (error) {
      setStatus("Error during login: " + (error as Error).message);
    }
  };

  const handleWithdraw = async () => {
    if (!cookies) {
      setStatus("Please login first to capture cookies.");
      return;
    }
    setStatus("Starting withdrawal process...");
    try {
      const api = new CsgoRollApi(cookies);
      // Example: get skins and withdraw with markup
      const skins = await api.getSkins();
      for (const skin of skins) {
        // Assuming skin has id property
        await api.withdrawSkin(skin.id, markup);
      }
      setStatus("Withdrawal process completed.");
    } catch (error) {
      setStatus("Error during withdrawal: " + (error as Error).message);
    }
  };

  return (
    <div className="max-w-md mx-auto p-4 bg-white shadow rounded">
      <h2 className="text-xl font-bold mb-4">Auto Withdraw Bot</h2>
      <button
        onClick={handleLogin}
        className="mb-4 px-4 py-2 bg-black text-white rounded hover:bg-gray-800"
      >
        Login to CSGOROLL
      </button>
      <div className="mb-4">
        <label htmlFor="markup" className="block mb-1 font-medium">
          Desired Markup (%)
        </label>
        <input
          id="markup"
          type="number"
          value={markup}
          onChange={(e) => setMarkup(Number(e.target.value))}
          className="w-full border border-gray-300 rounded px-3 py-2"
          min={0}
          step={0.01}
        />
      </div>
      <button
        onClick={handleWithdraw}
        className="px-4 py-2 bg-black text-white rounded hover:bg-gray-800"
      >
        Start Withdrawal
      </button>
      <p className="mt-4 text-gray-700">{status}</p>
    </div>
  );
}
