import axios from "axios";

export interface Cookie {
  name: string;
  value: string;
  domain?: string;
  path?: string;
  expires?: number;
  httpOnly?: boolean;
  secure?: boolean;
  sameSite?: "Strict" | "Lax" | "None";
}

export class CsgoRollApi {
  private cookies: Cookie[];

  constructor(cookies: Cookie[]) {
    this.cookies = cookies;
  }

  private getCookieHeader(): string {
    return this.cookies.map((cookie) => `${cookie.name}=${cookie.value}`).join("; ");
  }

  async getSkins() {
    // Example API call to get skins - adjust URL and headers as needed
    const response = await axios.get("https://csgo-roll.com/api/skins", {
      headers: {
        Cookie: this.getCookieHeader(),
        "User-Agent": "Mozilla/5.0",
      },
    });
    return response.data;
  }

  async withdrawSkin(skinId: string, markup: number) {
    // Example API call to withdraw skin with markup - adjust URL, method, and payload as needed
    const response = await axios.post(
      "https://csgo-roll.com/api/withdraw",
      {
        skinId,
        markup,
      },
      {
        headers: {
          Cookie: this.getCookieHeader(),
          "User-Agent": "Mozilla/5.0",
          "Content-Type": "application/json",
        },
      }
    );
    return response.data;
  }
}
