import puppeteer from 'puppeteer';

export async function captureCookies(url: string): Promise<string[]> {
  const browser = await puppeteer.launch({
    headless: false,
    defaultViewport: null,
    args: ['--start-maximized'],
  });
  const page = await browser.newPage();
  await page.goto(url);

  // Wait for user to login manually and then press Enter in the terminal to continue
  console.log('Please log in manually in the opened browser window. Press Enter here when done.');
  await new Promise<void>((resolve) => {
    process.stdin.once('data', () => resolve());
  });

  const cookies = await page.cookies();
  await browser.close();

  return cookies;
}
